import React from 'react'
import {CardDeck, Card , Button } from 'react-bootstrap'
import styled from 'styled-components'
import PlateGas from './assets/plate_gas.jpg'
import Welded from './assets/welded_plate2.jpg'
import Tube from './assets/tube.jpg'
import Gasket from './assets/gasket_Plate.jpg'
import Air_cooler from './assets/air_cooler.png'
import Spiral from './assets/Spiral.png'


import {Link} from 'react-router-dom'




const Styles = styled.div`

`

export const  Products = ({match}) => (
        <Styles className = "container">
            <h2>产品系列 </h2>
            <hr></hr>
            
            <CardDeck>

            <div className = "row">

            
                <Card >
                    <Card.Img variant="top" src={PlateGas} />
                    <Card.Body>
                    <Card.Title className = "text-center">板式气气换热器</Card.Title>
                    <Card.Text className = "text-center">
                    
                    
                    
                    <Link to = "products/PlateGasPage"><Button variant="primary">了解更多</Button></Link> 
                    
                    </Card.Text>
                    </Card.Body>
                    <Card.Footer>
                    <small className="text-muted">2019-09-17</small>
                    </Card.Footer>
                </Card>


                <Card>
                    <Card.Img variant="top" src={Welded} />
                    <Card.Body>
                    <Card.Title className = "text-center">全焊接板式换热器</Card.Title>
                    <Card.Text className = "text-center">
                    <Button variant="primary">了解更多</Button>
                    </Card.Text>
                    </Card.Body>
                    <Card.Footer>
                    <small className="text-muted">2019-09-17</small>
                    </Card.Footer>
                </Card>

                <Card >
                    <Card.Img variant="top" src= {Tube} />
                    <Card.Body>
                    <Card.Title className = "text-center">管板式换热器</Card.Title>
                    <Card.Text className = "text-center">
                    <Button variant="primary">了解更多</Button>
                    </Card.Text>
                    </Card.Body>
                    <Card.Footer>
                    <small className="text-muted">2019-09-17</small>
                    </Card.Footer>
                </Card>

                </div>
                <div className = "row">

                
                <Card  >
                    <Card.Img variant="top" src={Gasket} />
                    <Card.Body>
                    <Card.Title className = "text-center">垫片式板式换热器</Card.Title>
                    <Card.Text className = "text-center">
                    <Button variant="primary">了解更多</Button>
                    </Card.Text>
                    </Card.Body>
                    <Card.Footer>
                    <small className="text-muted">2019-09-17</small>
                    </Card.Footer>
                </Card>
                <Card  >
                    <Card.Img variant="top" src= {Air_cooler} />
                    <Card.Body>
                    <Card.Title className = "text-center">空气冷却/加热器 </Card.Title>
                    <Card.Text className = "text-center">
                    <Button variant="primary">了解更多</Button>
                    </Card.Text>
                    </Card.Body>
                    <Card.Footer>
                    <small className="text-muted">2019-09-17</small>
                    </Card.Footer>
                </Card>                
                <Card  >
                    <Card.Img variant="top" src={Spiral}/>
                    <Card.Body>
                    <Card.Title className = "text-center">螺旋扭曲管换热器</Card.Title>
                    <Card.Text className = "text-center">
                    <Button variant="primary">了解更多</Button>
                    </Card.Text>
                    </Card.Body>
                    <Card.Footer>
                    <small className="text-muted">2019-09-17</small>
                    </Card.Footer>
                </Card>
                </div>
                </CardDeck>
        </Styles>
    )
