import React from 'react'
import { Card,Alert } from 'react-bootstrap'
import styled from 'styled-components'

const Styles = styled.div`
  #head{
    font-weight: bold 
  }
`

export const  Career = () => (

    <Styles>

  <hr></hr>
  <Alert className="text-center" id = "head">
    职位信息
  </Alert>

<Card className="text-center">
 
  <Card.Body>
    
    <Card.Title>销售专员</Card.Title>
    <Card.Text>
    招聘条件：有换热器业内销售经验3年以上  <br></br>
    待遇：面议

    </Card.Text>
   
  </Card.Body>
  <Card.Footer className="text-muted">更新于1天前</Card.Footer>
</Card>

<br></br>
<Card className="text-center">

      <Card.Body>
          
        <Card.Title>生产助理</Card.Title>
        <Card.Text>
        招聘条件：具备生产协调能力和机械制造基本知识运用能力<br></br>
        待遇：面议

        </Card.Text>
      
      </Card.Body>
      <Card.Footer className="text-muted">更新于1天前</Card.Footer>
  </Card>


    </Styles>



)
