import React from 'react'
import {Jumbotron} from './components/Jumbotron'
import styled from 'styled-components'

import factory1 from './assets/factory.png'
import factory2 from './assets/factoryInside.jpg'

const Styles = styled.div`
        p{text-indent: 50px}
        img{
     
            justify-content:center 
            margin-left: auto;
            margin-right: auto;
            height: 300px
            width: 50%
            padding: 10px

        }

`


export const  About = () => (
        <div>
           
            <Jumbotron/>
            <Styles>

            <p>
                南京南钛热能技术有限公司主要从事于各种换热整体方案的解决，
                来源于德国多年来积累的技术和经验。
                南钛技术的根本在于高效的换热及热量的回收利用。
                专业的技术和广泛的经验可让我们为客户
                在石化、电力、能源、冶金、钢铁、食品、医药、制冷、暖通、船舶、机械
                等行业提供最优化的方案。
            </p>

            <img src = {factory1} alt = "factory"></img>
            <img src = {factory2} alt= "factory inside"></img>
            <p>
            南京南钛热能技术有限公司工厂位于南京市溧水区，占地近40000m2，
            其中厂房占地面积8000多平米。采用国内及国外标准进行生产制造。
            是集研发、生产、销售及售后于一体的现代化企业。
            </p>
            </Styles>
        </div>
    )
