import React, {useEffect, useState} from 'react'
import {Nav,Container} from 'react-bootstrap'


const Company = () => {

    
    const [display, setDisplay] = useState('bussniess_certification');
    function handleSelect(img_name){
        setDisplay(img_name)
    }



    // useEffect(() => {
    //    setDisplay(document.getElementById("display_img"))
    //   },[]);

    return (
        <div>

        <Nav justify variant="tabs" className="justify-content-center" defaultActiveKey = "link-0" >
            <Nav.Item>
                <Nav.Link  eventKey="link-0" onClick = {()=>{handleSelect('bussniess_certification')}} > 营业执照 </Nav.Link>
            </Nav.Item>

            <Nav.Item>
                <Nav.Link eventKey="link-4" onClick = {()=>{handleSelect('permission')}}> 开户许可证 </Nav.Link>
                 
            </Nav.Item>


            <Nav.Item>
                <Nav.Link eventKey="link-1" onClick = {()=>{handleSelect('iso9000')}} > ISO9000 证书</Nav.Link> 
                
            </Nav.Item>
           

            <Nav.Item>
                <Nav.Link eventKey="link-2" onClick = {()=>{handleSelect('env_admin')}}> 环境管理体系证书 </Nav.Link>
               
            </Nav.Item>

            <Nav.Item>
                <Nav.Link eventKey="link-3" onClick = {()=>{handleSelect('health_certificate')}} >职业健康安全证书</Nav.Link>
               
            </Nav.Item>
    

            <Container>
                <img style={{height:'100%',width:'100%'}} src= {require(`./assets/${display}.jpg`)} alt="证书" id = "display_img"/>
            </Container>  


        </Nav>

    </div>

    )

} 




export default Company ;