import React from 'react';

import {BrowserRouter  as Router , Route , Switch} from 'react-router-dom'


import {Home} from './Home';
import {About} from './About';
import {Contact} from './Contact';
import {NoMatch} from './NoMatch';
import {Career} from './Career' ; 
import {Products} from './Products'
import {Layout} from './components/Layout'
import {NavigationBar} from './components/NavigationBar'
// import {Jumbotron} from './components/Jumbotron'
import styled from 'styled-components'
import {PlateGasPage} from './PlateGas'
import Company from './Company'

const Styles = styled.div `
    min-height: 100vh; /* will cover the 100% of viewport */
    overflow: hidden;
    display: block;
    position: relative;
    padding-bottom: 70px; /* height of your footer */
    footer{
      
      display:flex 
      justify-content:center 
      

      width:100%;
      height:60px;   /* Height of the footer */
      position:absolute;
      left:0;
      bottom:0;
      right:0;
      
    }


`


function App() {
  return (
    <Styles>
      <NavigationBar />
      
      <Layout>
        <Router>
          <Switch>
          <Route  exact path = "/" component = {Home} />
          <Route  path = "/about" component = { About} />
          <Route  path = "/contact" component = { Contact } />
          <Route  path = "/career" component = {Career} />
          <Route  path = "/products" exact component = {Products} />
          <Route  path = "/company" exact component = {Company} /> 
          <Route  path= "/products/PlateGasPage" exact component={PlateGasPage} />
          {/* <Route  path = "/PlateGas" component = {PlateGas}/> */}
          <Route  component = {NoMatch } />

          </Switch>
        </Router>
        
      </Layout>
      <hr ></hr>
      
          <footer >
          
              <small> Nanjing NTECH Thermal Technology Co., Ltd &nbsp;<br/>

                  Copyright &copy; Sep. 2019, NTCH All Rights Reserved

              </small> 
        
        </footer> 
      </Styles>
    
  );
}

export default App;
