import React,{useState } from 'react'

import {Carousel} from 'react-bootstrap'
import styled from 'styled-components'

import forest2 from '../assets/forest2.jpg'
import machine from '../assets/machine.jpg'
import forest from '../assets/forest.jpg'
const CarouselWrapper = styled.div`
.carousel-control-next,
.carousel-control-prev {
    filter: invert(100%);
}


     .slogan1{
        filter: invert(100%);
    }

`;





export const ControlledCarousel = () => {
    const [index, setIndex] = useState(0);
    const [direction, setDirection] = useState(null);
  
    const handleSelect = (selectedIndex, e) => {
      setIndex(selectedIndex);
      setDirection(e.direction);
    };
  
    return (

        <CarouselWrapper>
                <Carousel  activeIndex={index} direction={direction} onSelect={handleSelect}>
                    <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src= {machine}
                        alt="First slide"
                    />
                    <Carousel.Caption>
                        <h3 className ="slogan1">高效热交换方案解决</h3>
                        <p className ="slogan1">Efficient Heat Transfer Solution</p>
                    </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={forest2}
                        alt="Second slide"
                    />
            
                    <Carousel.Caption>
                        <h3 className ="slogan2">专业          品质          高效</h3>
                        <p>Speciality      Quality      High-efficiency</p>
                    </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={forest}
                        alt="Third slide"
                    />
            
                    <Carousel.Caption>
                        <h3 className ="slogan3">以质量求生存 靠管理增效益</h3>
                        <p>
                        
                        </p>
                    </Carousel.Caption>
                    </Carousel.Item>
                </Carousel>
      </CarouselWrapper>
    );
  }
  
