import React from 'react'

import {Nav,Navbar, NavItem} from 'react-bootstrap'

import styled from 'styled-components'
import NavLink from 'react-bootstrap/NavLink';

const Styles = styled.div` 

    .navbar {
        background-color: white;
        color:black;
    }
    .navbar-brand, .navbar-nav .nav-link{

        color: black; 
        font-family: "Times New Roman", Times, serif;
        &:hover{
            font-weight:bold;
        }
    }

`;

export const NavigationBar = () => (

    <Styles>

    <Navbar expand = "lg" variant="light" >
        {/* <Navbar.Brand href = "/"> 南京钛能钛热技术有限公司</Navbar.Brand> */}
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
            <Nav className = "ml-auto">
                <NavItem><NavLink href = "/">首页</NavLink> </NavItem>
                <NavItem><NavLink href = "/about">关于公司</NavLink> </NavItem>
                <NavItem><NavLink href = "/company">公司资质</NavLink></NavItem>
                <NavItem><NavLink href = "/products">产品系列</NavLink> </NavItem>
                <NavItem><NavLink href = "/career">招聘信息</NavLink> </NavItem>
                <NavItem><NavLink href = "/contact">联系我们</NavLink> </NavItem>
                
                
            </Nav>

        </Navbar.Collapse>
        
    </Navbar>        
    </Styles>
)